import {authAPI} from "../../api/api";
import {stopSubmit} from "redux-form";
import Swal from 'sweetalert2';
import {handleLogin, handleLogout} from "../actions/auth";
import {getHomeRouteForLoggedInUser} from '@utils';
import React from "react";
import withReactContent from "sweetalert2-react-content";
import useJwt from '@src/auth/jwt/useJwt';
import {getUserProfile} from "./profile-reducer";
import {getNotifications, setNotif} from "./app-reducer";

const swal = withReactContent(Swal);

const SET_USER_DATA = '/build-the-world/auth/SET_USER_DATA';

const initialState = {
    userId: null,
    ecoId: null,
    login: null,
    isAuth: true
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            }
        default: {
            return state;
        }
    }
}
/*
    Установить полученные данные пользователя.
 */
export const setAuthUserData = (userId, ecoId, login, isAuth = true) => ({type: SET_USER_DATA, payload: {userId, ecoId, login, isAuth}});
/*
    Выход из аккаунта.
    Очищаются все переменные пользователя.
 */
export const logout = (history = null) => async (dispatch) => {
    const response = await authAPI.logout();
    if (response.data.resultCode === 1) {
        dispatch(handleLogout());
        dispatch(setAuthUserData(null, null, null, false));
        dispatch(setNotif([]));
        if (history !== null) {
            history.push('/login');
        }
    }
}
/*
    Проверка на авторизацию пользователя.
    Если пользователь авторизован, заносим его id, idEco, login в state.
    Авторизуемся в панель.
    Если пришла история пути, отправляет откуда пришел, выкидываем сообщение об успешной авторизации, иначе оставляем.
    Если не авторизован, выходом очищаем все возможные переменные пользователя.
    @history = useHistory();
    @ability = useContext(AbilityContext);
 */
export const getAuthUserData = (history, ability) => async (dispatch) => {
    const email = 'user@demo.com';
    const password = 'user';
    const response = await authAPI.me();
    if (response.data.resultCode === 1) {
        const {user_id, user_id_fromEco, user_login} = response.data.data;
        dispatch(getUserProfile(user_id));
        dispatch(setAuthUserData(user_id, user_id_fromEco, user_login));
        dispatch(getNotifications(user_id));
        useJwt.login({email, password}).then(res => {
            const data = {...res.data.userData, accessToken: res.data.accessToken, refreshToken: res.data.refreshToken};
            dispatch(handleLogin(data));
            if (ability !== null) ability.update(res.data.userData.ability);
            if (history !== null) {
                history.push(getHomeRouteForLoggedInUser(data.role));
            }
        }).catch(err => console.log(err));
    } else {
        dispatch(stopSubmit("me", {_error: response.data.message}));
        dispatch(logout(history));
        if (history !== null) history.push('/login');
    }
}
/*
    Авторизация пользователя.
    Если атворизация успешна, дергаем проверку на авторизацию, передаем историю пути, возвращаем пользователя на главную страницу.
    Иначе выводим сообщение с ошибкой.
    @useObject = {user_login, user_pwd};
    @history = useHistory();
    @ability = useContext(AbilityContext);
 */
export const login = (userObject, history, ability) => async (dispatch) => {
    const response = await authAPI.login(userObject);
    if (response.data.resultCode === 1) {
        dispatch(getAuthUserData(history, ability));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("login", {_error: message}));
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "ОК"
        });
    }
}
/*
    Регистрация пользователя.
    Если регистрация успешна, дергаем проверку на авторизацию, передаем историю пути, возвращаем пользователя на главную страницу.
    Иначе выводим сообщение с ошибкой.
    @useObject = {
        user_name, user_phone, user_social,
        user_city, user_school, user_class,
        user_teacher, user_teacherPosition, user_email,
        user_login, user_pwd
    };
    @history = useHistory();
    @ability = useContext(AbilityContext);
 */
export const register = (userObject, history, ability) => async (dispatch) => {
    const response = await authAPI.register(userObject);
    if (response.data.resultCode === 1) {
        dispatch(getAuthUserData(history, ability));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("register", {_error: message}));
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "ОК"
        })
    }
}
/*
    Привязка аккаунта EcoWorld к уже зарегистрированному аккаунту.
    При успешном выполнении дергаем проверку на авторизацию, передаем историю пути, возвращаем пользователя на главную страницу.
    Иначе выводим сообщение с ошибкой.
    @ecoId = user_id от аккаунта EcoWorld;
    @history = useHistory();
    @ability = useContext(AbilityContext);
 */
export const bindEco = (ecoId, history, ability) => async (dispatch) => {
    const response = await authAPI.setEcoId(ecoId);
    const message = response.data.message;
    if (response.data.resultCode === 0) {
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "ОК"
        });
        dispatch(stopSubmit("login", {_error: message}));
    } else {
        await swal.fire({
            title: "Успех!",
            text: message,
            icon: "success",
            confirmButtonText: "Перейти"
        }).then(() => {
            dispatch(getAuthUserData(history, ability));
        });
    }
    return response.data;
}
/*
    Авторизация через аккаунт с EcoWorld.
    Ищет по логину и паролю аккаунт в базе EcoWorld.
    При удачном поиске возвращает найденый аккаунт.
    Иначе выводим сообщение с ошибкой.
    @useObject = {user_login, user_pwd};
 */
export const loginecoworld = (userObject, isAuth, history, ability) => async (dispatch) => {
    const response = await authAPI.loginecoworld(userObject);
    if (response.data.resultCode === 1) {
        if (isAuth) {
            const {user_id} = response.data.data;
            dispatch(bindEco(user_id, history, ability));
        } else {
            dispatch(register(response.data.data, history, ability));
        }
    } else {
        const message = response.data.message;
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "ОК"
        });
        dispatch(stopSubmit("login", {_error: message}));
    }
}

export const changepassword = (userId, userLogin, currPassword, newPassword) => async (dispatch) => {
    const response = await authAPI.changePassword(userId, userLogin, currPassword, newPassword);
    const message = response.data.message;
    if (response.data.resultCode === 1) {
        await swal.fire({
            title: "Успех!",
            text: message,
            icon: "success",
            confirmButtonText: "ОК"
        }).then(() => {
            const userObject = {user_login: userLogin, user_pwd: newPassword};
            dispatch(login(userObject, null, null));
        });
    } else {
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "ОК"
        });
        dispatch(stopSubmit("changePassword", {_error: message}));
    }
}


export default authReducer;